@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@900&family=Source+Sans+3:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,500;1,600&display=swap");

.hero {
  &__section {
    background: rgb(30, 24, 46);
    background: linear-gradient(
      90deg,
      rgba(30, 24, 46, 1) 42%,
      rgba(23, 30, 55, 1) 77%
    );
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__stars {
    position: absolute;
    top: 0;
    left: 0;
    width: 200vw;
    height: 120%;
    z-index: 1;
  }

  &__star {
    $star-count: 58;
    --star-color: white;
    --star-tail-length: 6em;
    --star-tail-height: 2px;
    --star-width: calc(var(--star-tail-length) / 6);
    --fall-duration: 9s;
    --tail-fade-duration: var(--fall-duration);

    position: absolute;
    top: var(--top-offset);
    left: 0;
    width: var(--star-tail-length);
    height: var(--star-tail-height);
    color: var(--star-color);
    background: linear-gradient(45deg, currentColor, transparent);
    border-radius: 50%;
    filter: drop-shadow(0 0 6px currentColor);
    transform: translate3d(calc(100vw + 30em), 0, 0);
    animation: fall var(--fall-duration) var(--fall-delay) linear infinite,
      tail-fade var(--tail-fade-duration) var(--fall-delay) ease-out infinite;

    @media screen and (max-width: 750px) {
      animation: fall var(--fall-duration) var(--fall-delay) linear infinite;
    }

    @for $i from 1 through $star-count {
      &:nth-child(#{$i}) {
        --star-tail-length: #{random_range(500em, 750em) / 100};
        --top-offset: #{random_range(0vh, 10000vh) / 100};
        --fall-duration: #{random_range(6000, 12000s) / 1000};
        --fall-delay: #{random_range(0, 10000s) / 1000};
      }
    }

    &::before,
    &::after {
      position: absolute;
      content: "";
      top: 0;
      left: calc(var(--star-width) / -2);
      width: var(--star-width);
      height: 100%;
      background: linear-gradient(45deg, transparent, currentColor, transparent);
      border-radius: inherit;
      animation: blink 2s linear infinite;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }

  &__content {
    max-width: 1200px;
    margin: auto;
    display: flex;
    align-items: center;
  }

  &__logo-box {
    height: 100vh;
    width: 50vw;
    display: flex;
    align-items: center;
  }

  &__img-box {
    position: relative;
    width: 450px;
    height: 450px;
  }

  &__profile {
    height: 400px;
    position: absolute;
    left: 200px;
    top: 20px;
    opacity: 1;
    animation: bounceInUp;
    animation-duration: 1s;
    z-index: 2;
    --animate-delay: 2s;
  }

  &__arrow1 {
    position: absolute;
    left: 100px;
    height: 130px;
    top: 150px;
    opacity: 1;
    animation: backInLeft;
    animation-duration: 1s;
    z-index: 1;
  }

  &__arrow2 {
    position: absolute;
    left: 100px;
    height: 130px;
    top: 273px;
    opacity: 1;
    animation: backInLeft;
    animation-duration: 1s;
    z-index: 3;
  }

  &__arrow3 {
    position: absolute;
    left: 370px;
    height: 130px;
    top: 229px;
    opacity: 1;
    animation: backInRight;
    animation-duration: 1s;
    z-index: 1;
  }

  &__arrow4 {
    position: absolute;
    left: 370px;
    height: 130px;
    top: 353px;
    opacity: 1;
    animation: backInRight;
    animation-duration: 1s;
    z-index: 3;
  }

  &__slash {
    position: absolute;
    left: 265px;
    top: 30px;
    height: 450px;
    opacity: 1;
    animation: bounceInDown;
    animation-duration: 1s;
    z-index: 1;
  }

  &__text-box {
    height: 100vh;
    width: 50vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
  }

  &__title {
    color: #5df8f5;
    font-size: 45px;
    font-weight: 350;
    margin-bottom: 25px;
    text-align: left;
  }

  &__title-highlight {
    color: #ff3870;
  }

  &__btn {
    box-sizing: border-box;
    height: 50px;
    padding: 10px 20px;
    background-color: transparent;
    border: 2px solid white;
    color: white;
    font-size: 20px;
    cursor: pointer;
    margin: 0 auto;
    display: flex;
    align-items: center;

    &:hover {
      background-color: #ff3870;
      border: none;
    }
  }

  &__btn:hover &__btn-arrow {
    transform: rotate(90deg);
  }

  &__btn-arrow {
    margin-left: 10px;
    transition: transform 0.5s ease;
  }
}

@function random_range($min, $max) {
  $rand: random();
  $random_range: $min + floor($rand * (($max - $min) + 1));
  @return $random_range;
}

@keyframes fall {
  to {
    transform: translate3d(-30em, 0, 0);
  }
}

@keyframes tail-fade {
  0%,
  50% {
    width: var(--star-tail-length);
    opacity: 1;
  }

  70%,
  80% {
    width: 0;
    opacity: 0.4;
  }

  100% {
    width: 0;
    opacity: 0;
  }
}

@keyframes blink {
  50% {
    opacity: 0.6;
  }
}

@media screen and (max-width: 992px) {
  .hero {
    &__section {
      height: auto;

      .hero__stars {
        height: auto;
      }
    }

    &__content {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      align-items: flex-end;
      width: 100%;
    }

    &__logo-box {
      height: 50vh;
      width: 100%;
      display: flex;
      justify-content: center;
    }

    &__img-box {
      width: 330px;
      height: 330px;
      position: relative;
    }

    &__profile {
      left: 70px;
      top: 60px;
      height: 230px;
    }

    &__arrow1 {
      left: 20px;
      height: 70px;
      top: 135px;
    }

    &__arrow2 {
      left: 20px;
      height: 70px;
      top: 200px;
      opacity: 1;
    }

    &__arrow3 {
      left: 180px;
      height: 70px;
      top: 175px;
    }

    &__arrow4 {
      left: 180px;
      height: 70px;
      top: 240px;
    }

    &__slash {
      left: 120px;
      top: 60px;
      height: 250px;
      width: 90px;
    }

    &__text-box {
      padding-top: 10px;
      padding-left: 0px;
      width: 100%;
      height: 50vh;
      justify-content: flex-start;
    }

    &__title {
      font-size: 30px;
      margin-top: 30px;
      text-align: center;
      margin-left: 0px;
    }
  }
}

